export default {
	data() {
		return {
			tobindex: 2,
			itUrl: '',
			code: ''
		};
	},
	onLoad() {
		let that = this;
		that.itUrl = window.location.href;
		if (new RegExp('.*\\b' + 'code' + '\\b(\\s*=([^&]+)).*', 'gi').test(that.itUrl)) {
			// 先判断是否为回调
			that.code = RegExp.$2;
			uni.setStorageSync('code',that.code);
			that.login()
		}else{
			// 判断登录
			if (uni.getStorageSync('unionid')) {
				//登录过
				console.log('登录过');
			} else {
				// 未登录
				that.GzhLogin();
			}
		}
	},
	methods: {
		login(){
			this.islogin=false;
			uni.request({
				url: 'https://xhj.ninefishing.com/vote/index/getaccesstoken',
				method: 'POST',
				data: {
					code: this.code
				},
				header: {
					'Content-Type': 'application/json'
				},
				success: res => {
					// 返回的数据
					console.log(res, 'getaccesstoken');
					uni.setStorageSync('unionid',res.data.unionid)
					uni.setStorageSync('user_id',res.data.user_id)					
				}
			});
		},
		select(i) {
			this.tobindex = i;
		},
		gotovote() {
			uni.switchTab({
				url: '../vote/vote'
			});
		},
		GzhLogin() {
			// 微信内浏览器（公众号）获取code
			let that = this;
			uni.request({
				url: 'https://xhj.ninefishing.com/vote/index/getCode', 
				method: 'POST',
				data: {
					callback: that.itUrl
				},
				header: {
					'Content-Type': 'application/json'
					// 'X-ECTouch-Authorization': token
				},
				success: res => {
					// 返回的数据
					console.log(res.data);
					window.location.href = res.data;
				}
			});
		}
	}
};
